'use strict';
import Pageable from 'module-crud-ui/app/scripts/crud.pageable.js';

export default class SearchResultsTabsTechnicalCtrl extends Pageable {

    /*@ngInject*/
    constructor($injector, api, $state){
        super({injector: $injector, endpoint: 'technicalStateAudit', params: {'id': $state.params.id}, executeGet: false});

        this.api = api;
        this.state = $state;
      if(this.state.params.id){
        this.get();
      }
    }

    getPathParams(){
        return { 'detailCid': this.state.params.id };
    }

    getDetailData(endpoint){
        this.response = this.api[endpoint].get(this.getPathParams());
        this.response.isLoading = true;
        var objectData = this.response;

        this.response.$promise.then(() => {

          objectData.isLoading = false;
          objectData.valid = true;

        }, function(error) {
          // Si nos devuelve un 404, no queremos que se muestre el recurso, pero tampoco que nos tire error
          if(error.status == 404){
            objectData.valid = false;
            objectData.isLoading = false;
          }
        });
        return this.response;
    }
}

